/* General Page Styling */
body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #f4f4f4;
}

h1, h2, h3 {
  font-weight: bold;
  color: #000;
}

/* Page Wrapper */
.page-container {
  padding: 40px;
  text-align: center;
}

/* About Page Styling */
.about-page {
  text-align: center;
  padding: 50px;
}

.about-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  max-width: 800px;
  margin: auto;
}

.about-image-container {
  display: flex;
  justify-content: center;
}

.about-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.about-text {
  font-size: 1.1rem;
  line-height: 1.7;
  max-width: 700px;
  text-align: justify;
}

/* Resume Button */
.resume-button {
  display: inline-block;
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #0a48a7;
  color: white;
  text-decoration: none;
  border-radius: 8px;
  font-weight: bold;
  transition: 0.3s ease-in-out;
}

.resume-button:hover {
  background-color: #062e6f;
}

/* Contact Form Styling */
.contact-form {
  max-width: 450px;
  margin: auto;
  text-align: left;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.contact-form button {
  width: 100%;
  padding: 10px;
  background-color: #0a48a7;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: 0.3s;
}

.contact-form button:hover {
  background-color: #062e6f;
}

/* Footer Styling */
.footer {
  background-color: #f1f1f1;
  padding: 15px 0;
  text-align: center;
  font-size: 0.9rem;
  color: #333;
  margin-top: 30px;
}

/* Responsive Styling */
@media (max-width: 768px) {
  .about-content {
    flex-direction: column;
  }

  .about-image {
    width: 150px;
    height: 150px;
  }
}
#root {
  max-width: 1200px;
  margin: auto;
  width: 100%;
}