.project-page {
  padding: 20px;
  max-width: 800px;
  margin: auto;
  text-align: left;
  background: white;
  border-radius: 8px;
}

.project-page h1 {
  color: #0d47a1;
  text-align: center;
}

.project-details {
  padding-left: 20px;
  margin: 15px 0;
}

.project-link {
  display: inline-block;
  margin-top: 10px;
  color: #0d47a1;
  font-weight: bold;
  text-decoration: none;
}

.project-link:hover {
  text-decoration: underline;
}

/* Project Cards on Projects Page */
.projects-page ul {
  list-style: none;
  padding: 0;
}

.projects-page ul li {
  margin: 10px 0;
}

.projects-page ul li a {
  color: #0d47a1;
  font-weight: bold;
  text-decoration: none;
}

.projects-page ul li a:hover {
  text-decoration: underline;
}
